export default {
	data() {
		return {
			List:[],
		}
	},
	onLoad:function(){
			var _self = this
			_self.getInfo()
	},
	methods: {
		getInfo:function(){
			var _self = this
			uni.request({ 
				url:_self.$api+"dockingManager/totalQuery",
				data:{id:"0",pull:3,optionId:uni.getStorageSync("openId"),branch:0},
				method:"GET",
				success:function(res){
					_self.List = res.data
					console.log(res.data)
				}
			})
		},
		goDetail:function(id){
			console.log(id)
			uni.navigateTo({
				url:"../lawServerDetail/lawServerDetail?id="+id
			})
		},
	}
}
